/* Global Styles */
body {
  font-family: 'Arial', sans-serif;
  margin: 0;
  padding: 0;
  color: #ffffff;
  background-color: #004ba0;
}

.App {
  text-align: center;
  color: white;
}

/* Header and Banner Styling */
.header {
  position: relative;
}

.banner-img {
  width: 100%;
  height: auto;
}

/* Navigation Bar */
nav {
  background-color: #004ba0;
  padding: 10px 0;
}

nav ul {
  list-style: none;
  display: flex;
  justify-content: center;
  padding: 0;
}

nav ul li {
  margin: 0 15px;
}

nav ul li a {
  color: #ffffff;
  text-decoration: none;
  font-size: 1.2rem;
}

nav ul li a:hover {
  text-decoration: underline;
}

/* Resume Section */
.resume {
  padding: 40px;
  text-align: left;
  max-width: 800px;
  margin: 0 auto;
}

.resume h2 {
  font-size: 2rem;
  margin-bottom: 10px;
}

.resume h3 {
  font-size: 1.5rem;
  margin-top: 20px;
}

.resume p, .resume ul {
  font-size: 1rem;
  line-height: 1.6;
  margin: 10px 0;
}

.resume ul {
  list-style: disc;
  padding-left: 20px;
}

/* Projects Section */
.projects {
  padding: 50px;
}

.projects ul {
  list-style: none;
  padding: 0;
}

.projects ul li {
  margin: 20px 0;
}

.projects ul li strong {
  font-size: 1.2rem;
}

.projects ul li p {
  font-size: 1rem;
}

/* Contact Section */
.contact {
  padding: 50px;
}

.contact p {
  font-size: 1.2rem;
}

/* Responsive Styles */
@media only screen and (max-width: 768px) {
  nav ul {
    flex-direction: column;
  }

  nav ul li {
    margin: 10px 0;
  }

  .resume, .projects, .contact {
    padding: 20px;
  }

  .resume h2 {
    font-size: 1.5rem;
  }

  .resume h3 {
    font-size: 1.2rem;
  }

  .resume p, .resume ul {
    font-size: 0.9rem;
  }

  .projects ul li strong {
    font-size: 1rem;
  }

  .projects ul li p {
    font-size: 0.9rem;
  }
 /* Button Styling */
.button {
  display: inline-block !important;
  padding: 12px 24px !important;
  background-color: #004ba0 !important; /* Blue color that matches the theme */
  color: white !important;
  text-decoration: none !important;
  font-size: 1.1rem !important;
  font-weight: bold !important;
  border-radius: 30px !important;
  border: 2px solid transparent !important;
  transition: all 0.3s ease !important;
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.2) !important;
  margin: 10px !important;
}

/* Hover effect */
.button:hover {
  background-color: white !important;
  color: #004ba0 !important;
  border: 2px solid #004ba0 !important;
  box-shadow: 0px 6px 12px rgba(0, 0, 0, 0.3) !important;
  transform: translateY(-2px) !important;
}
}